@font-face {
  font-family: myFont;
  src: url('./components/skrapbook/Skrapbook.ttf');
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
/* general setting  */
* {
  font-family: myFont;
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}



a {
  text-decoration: none;
  color: black;
}
a:active, a:hover {
  text-decoration: underline;
}

button {
  padding: 5px 10px;
  outline: none;
  background-color: white;
  border-radius: 10px;
  border: none;
}

button:hover, 
button:active {
  box-shadow: inset 0px 0px 5px black;
}

ul {
  list-style-type: none;
}
/* general setting  */
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
  display: grid;
  grid-template-rows: 150px 1fr 50px;
  min-height: 100vh;
  /* overflow: auto; */
  gap: 1rem;
}
 /* navbar  */
@media screen and (min-width: 800px) {
  :root {
    font-size: 18px;
  }

  .nav-container {
    display: grid;
    grid-template-rows: 3fr 1fr;
  }

  .nav {
    background-color: yellow;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
    align-items: center;
  }

  .nav-logo {
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  
  .nav-logo-img {
    width: 70px;
    height: 70px;
    border-radius: 100%;
  }
  
  .nav-social-icons {
    display: flex;
    gap: 1rem;
  }
  
  .social-icons {
    width: 40px;
    height: 40px;
    border-radius: 100%;
  }
  
  .nav-login {
    display: flex;
    gap: 1rem;
    align-items: center;
  }
  
  .nav-login-btn {
    background: white;
    outline: none;
    border: none;
    padding: 5px 10px;
    border-radius: 10px;
  }
  
  .nav-searchbar {
    width: 100%;
  }
  
  .nav-container {
    display: grid;
    grid-template-rows: 3fr 1fr;
  }
  
  .nav-menu {
    display: flex;
    gap: 1rem;
    justify-content: center;
    align-items: center;
    background-color: white;
  }
}

@media screen and (max-width: 800px) { 
  :root {
    font-size: 12px;
  }

  .nav-container {
    display: grid;
    grid-template-rows: 2fr 1fr 1fr;
  }

  .nav {
    background-color: yellow;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem;
  }

  .nav-logo {
    display: flex;
    align-items: center;
  }
  
  .nav-logo-img {
    width: 70px;
    height: 70px;
    border-radius: 100%;
  }
  
  .nav-social-icons {
    display: flex;
  }
  
  .social-icons {
    width: 40px;
    height: 40px;
    border-radius: 100%;
  }
  
  .nav-login {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    background-color: yellow;
    padding-bottom: 10px;
  }

  .nav-searchbar {
    width: 100%;
  }
  
  .nav-menu {
    display: flex;
    gap: 1rem;
    justify-content: center;
    align-items: center;
    background-color: white;
    padding: 5px;
  }
  .menu-btn {
    width: 40px;
    height: 40px;
  }

  .toggledMenu {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
  }
  .nav-login-btn {
    background: white;
    outline: none;
    /* border: 1px solid black; */
    border: none;
    padding: 5px 10px;
    border-radius: 10px;
  }
}



