/* main  */

.main-container {
  background-color: wheat;
}
/* main  */

/* pagination */

@media screen and (min-width: 800px) {
  .details {
    position: absolute;
    width: 90%;
    height: 90%;
    background-color: aqua;
    overflow: auto;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }
  
  .main {
    display: grid;
    grid-template-columns: 1fr 4fr;
    position: relative;
  }

  .pagination-items {
    display: grid;
    gap: 1rem;
  }

  .pagination {
    padding: 1rem;
    display: grid;
    grid-template-rows: 10px 1fr 10px;
    background-color: RGB(230, 230, 230);
    /* justify-items: center; */
    gap: 1rem;
  }

  .pagination-items {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 1fr);
  }  
}

@media screen and (max-width: 800px) {
  .details {
    position: absolute;
    width: 30vw;
    height: 30vh;
    background-color: aqua;
    overflow: auto;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }
  
  .main {
    display: grid;
    position: relative;
  }

  .pagination-items {
    display: grid;
    gap: 1rem;
    justify-items: center;
  }
  
  .pagination {
    padding: 1rem;
    display: grid;
    grid-template-rows: 10px 1fr 10px;
    background-color: white;
    /* justify-items: center; */
    gap: 1rem;
  }

  .pagination-items {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 1fr);
  }  
}


.pagination-menu {
  display: flex;
  gap: .5rem;
  justify-content: center;
  align-items: center;
}

.pagination-active-page {
  transform: scale(1.1);
}

/* pagination  */

/* cards  */
.card {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  justify-items: center;
  align-items: center;
  position: relative;
}

.card-texts {
  width: 80%;
  height: 100%;
  display: grid;
  grid-template-rows: 50px 1fr
}

.card-text {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 0.1rem;
}

.card-btn-group {
  display: flex;
  justify-content: center;
}

.detail-container {
  border: 1px solid black;
}

.details-title {
  text-align: end;
}

.details-des {
  text-align: end;
}

.details-btn-group {
  display: flex;
  justify-content: center;
}

/* cards  */

/* slider  */
.horizontal-slider {
  width: 100%;
  max-width: 500px;
  height: 100vh;
  margin: auto;
}
.example-thumb {
  cursor: pointer;
  position: absolute;
  z-index: 100;
  background: #ffffff;
  border: 5px solid #3774ff;
  border-radius: 100%;
  display: block;
  box-shadow: 0 0 2px 0 rgb(0 0 0 / 44%);
}
.example-thumb.active {
  background-color: grey;
}
.example-track {
  position: relative;
  background: #ddd;
}
.example-track.example-track-0 {
  background: #83a9ff;
}
.horizontal-slider .example-track {
  top: 20px;
  height: 4px;
}
.horizontal-slider .example-thumb {
  top: 12px;
  width: 10px;
  outline: none;
  height: 10px;
  line-height: 38px;
}
/* slider  */

/* side menu  */

@media screen and (min-width: 800px) {
  .side-menu {
    background-color: RGB(230, 230, 230);
    display: grid;
    grid-template-rows: 50px 1fr;
    padding: 1rem;
  }

  .side-menu-list {
    display: grid;
    gap: .8rem;
  }

  .side-menu-li {
    font-size: 1.3rem;
  }
}

@media screen and (max-width: 800px) {
  .side-menu {
    background-color: RGB(230, 230, 230);
    display: grid;
    grid-template-rows: 50px 1fr;
    padding: 1rem;
  }

  .side-menu-list {
    display: grid;
    gap: .8rem;
  }

  .side-menu-li {
    font-size: 1.3rem;
  }
}

/* .toggled-sidemenu {
  position: absolute;
} */

.side-toggled {
  position: absolute;
  z-index: 1;
}

.side-menu-options {
  display: flex;
}

@keyframes load {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.loading {
  /* background-color: yellow; */
  width: 50px;
  height: 50px;
  background-color: yellow;
  animation-name: load;
  animation-duration: .1s;
  animation-iteration-count: infinite;
  animation-fill-mode: backwards;
  animation-timing-function: linear;
  transform-origin: right center;
}

/* side menu  */